html {
    font-size: 62.5%;
}

body {
    background-color: #20232a;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
}

main {
    text-align: center;
}

h1 {
    font-size: 2.4rem;
    text-align: center;
}

ul {
    list-style: none;
    padding: 0;
    width: 100%;
    text-align: left;
    max-width: 550px;
    margin: 0 auto;
}

ul li {
    font-size: 1.8rem;
}

p {
    text-decoration: underline;
    font-size: 1.4rem;
}

p:last-of-type {
    text-decoration: none;
}

form {
    margin-top: 35px;
}

.input-text {
    width: 210px;
    padding: 5px;
    text-align: center;
    outline: none;
}

button {
    padding: 5px;
    border-radius: 5px;
    border: none;
    width: 30px;
    margin-left: 2px;
}

button:first-of-type {
    background-color: green;
}

button:last-of-type {
    background-color: #ff5722;
}

[type="checkbox"] {
    display: none;
}

label {
    padding-left: 5px;
    position: relative;
}

label::before {
    content: '';
    border: 2px solid white;
    border-radius: 3px;
    width: 15px;
    height: 15px;
    display: inline-block;
    margin-right: 5px;
    position: relative;
    top: 2px;
}

[type="checkbox"]:checked + label::before{
    color: green;
    border-color: green;
    content: '✔';
    font-size: 1rem;
    top: -2px;
    text-align: center;
}

@media (min-width: 768px) {
    .input-text {
        width: 280px;
    }
}

@media (min-width: 1024px) {
    main {
        max-width: 1250px;
        margin: 0 auto;
    }
    
    h1 {
        font-size: 3.6rem;
    }

    ul {
        max-width: 857px;
    }

    ul li {
        font-size: 2.8rem;
    }

    .input-text {
        width: 500px;
        padding: 10px;
        font-size: 2.2rem;
    }

    p {
        font-size: 2rem;
    }

    button {
        width: 50px;
        padding: 15px;
        margin-left: 8px;
    }

    label::before {
        content: '';
        border: 2px solid white;
        border-radius: 3px;
        width: 20px;
        height: 20px;
        display: inline-block;
        margin-right: 10px;
        position: relative;
        top: 2px;
    }

    [type="checkbox"]:checked + label::before{
        color: green;
        border-color: green;
        content: '✔';
        font-size: 1.5rem;
        top: -2px;
        text-align: center;
    }
}

@media (min-width: 1440px){
    h1 {
        font-size: 4.5rem;
    }

    p {
        font-size: 3rem;
    }

    .input-text {
        font-size: 3.4rem;
        width: 950px;
        padding: 20px;
    }

    ul li {
        font-size: 4rem;
    }

    button {
        width: 90px;
        padding: 35px;
        font-size: 2rem;
    }

    label::before {
        border-width: 3px;
        width: 30px;
        height: 30px;
    }

    [type="checkbox"]:checked + label::before{
     font-size: 2.5rem;
    }

}